@import '../../scss/vars.scss';
@import '../../scss/mixins.scss';

.cardItem {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 40px 15px 30px 15px;
    border-radius: 15px;
    max-width: 340px;

    &:hover {
        @include transition;
        @include shadow($grey_shades_40);
        @include scale_increase;
        
        background-color: $general_background;

        .cardItem__title {
            color: $white;
        }

        .cardItem__desc {
            color: $white;
        }
    }

    &__img {
        width: 100%;
        height: 160px;
    }

    &__textPanel {
        display: flex;
        flex-direction: column;
        gap: 10px;
        border-right: solid 2px $grey_shades_30;
        padding: 20px 0 25px 10px;
        height: 170px;
    }

    &__title {
        @include text_regular;
    }

    &__desc {
        @include text_small;
        font-weight: 400;
    }

    &__noDataTitle {
        width: 50%;
        height: 10px;
        background-color: $grey_shades_20;
    }

    &__noDataDesc {
        width: 80%;
        height: 10px;
        background-color: $grey_shades_20;
    }
}

.noHoverCardItem {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 40px 15px 30px 15px;
    border-radius: 15px;
}

