@import '../../scss/vars.scss';
@import '../../scss/mixins.scss';

.newsletter {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 1000;

    &__overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: $grey_shades_100;
        opacity: 0.7;
    }

    &__hide {
        top: -100vh;
    }

    &__show {
        top: 0;
    }

    &__panel {
        display: flex;
        flex-direction: column;
        background-color: $white;
        max-width: 560px;
        width: 100%;
        height: fit-content;
        z-index: 1001;
        padding: 25px 18px 25px 18px;
        border-radius: 10px;

        .text {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        &Img {
            width: 100px;
        }

        &Text {
            text-align: center;

            @include text-regular;
        }
    }
}