@import '../../scss/vars.scss';
@import '../../scss/mixins.scss';

.partnerCard {
    display: flex;
    align-items: center;
    gap: 20px;
    background-color: bisque;
    width: fit-content;
    height: 300px;
    padding: 2rem;
    border-radius: 10px;

    // &__general {

    // }
  
    &__img {
        margin-left: -20%;
        width: 350px;
        height: 200px;
        border-radius: 20px;
    }
    
    &__title {
        margin: 0;
        margin-top: 1rem;
    }
    
    &__text {
        margin: 0;
        margin-top: 0.5rem;
        margin-bottom: 1.5rem;
    }
}

@media screen and (max-width: $md) {
    .partnerCard {
    
        &__img {
            width: 300px;
            height: 150px;
        }
    }
}

@media screen and (max-width: $sm) {
    .partnerCard {
        height: auto;
        flex-direction: column;
    
        &__img {
            margin-left: 0%;
            width: 200px;
            height: 150px;
            border-radius: 20px;
        }
        
        &__title {
            font-size: 20px;
        }
    }
}