@import '../../scss/vars.scss';
@import '../../scss/mixins.scss';

.swipeSlider {
    width: 100%;
    height: 560px;
}

.swiper {
    width: 100%;
    height: 100%;
}
  
.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 560px;
    object-fit: cover;
}
  
@media screen and (max-width: $sm) {
    .swipeSlider {
        height: 300px;
    }

    .swiper-slide img {
        height: 300px;
    }
}