@import '../../../scss/vars.scss';
@import '../../../scss/mixins.scss';

.homePartner {
    width: 100%;
    padding: 70px 0 70px 0;

    &__header {
        width: 100%;
        padding: 20px 0 20px 0;

        @include flex-center;

        &Title {
            @include text_bigTitle;
            @include text_shadow;

            font-family: $Plateia;
            color: $grey_shades_80;
        }
    }

    &__container {
        // display: grid;
        // grid-template-columns: repeat(4, 1fr);
        // gap: 20px;
    }

    &__footer {
        width: 100%;

        @include flex-center;
    }
}

