@import '../../../scss/vars.scss';
@import '../../../scss/mixins.scss';

.partnerItem {
    display: grid;
    grid-template-columns: 400px calc(100% - 700px) 300px;
    border-bottom: 2px solid $grey_shades_30;
    width: 100%;
    padding: 20px 0 20px 0;

    &__imgPanel {
        padding: 20px 35px 20px 35px;
    }

    &__img {
        width: 100%;
    }

    &__general {
        @include flex-center;

        flex-direction: column;
        align-items: start;
        width: 100%;

        &Partner {
            @include text_bigTitle;
        }

        &Website {
            @include text_title;
            
            color: $blue;
        }

        &Addr {
            @include text_title;
        }
    }

    &__contact {
        @include flex-center;

        flex-direction: column;
        align-items: end;
        width: 100%;

        &Num {
            @include text_title;
        }

        &Fax {
            @include text_title;
        }
    }
}

@media screen and (max-width: $md) {
    .partnerItem {
        grid-template-columns: 250px calc(100% - 450px) 200px;
    
        &__general {
    
            &Partner {
                @include text_title;
            }
    
            &Website {
                @include text_regular;
                
                color: $blue;
            }
    
            &Addr {
                @include text_regular;
            }
        }
    
        &__contact {

            &Num {
                @include text_regular;
            }
    
            &Fax {
                @include text_regular;
            }
        }
    }
}

@media screen and (max-width: $sm) {
    .partnerItem {
        grid-template-columns: 250px calc(100% - 250px);
    
        &__contact {
            display: none;
        }
    }
}

@media screen and (max-width: $xs) {
    .partnerItem {
        grid-template-columns: 200px calc(100% - 200px);


    
        &__general {
    
            &Partner {
                @include text_regular;
            }
    
            &Website {
                @include text_small;
                
                color: $blue;
            }
    
            &Addr {
                @include text_small;
            }
        }
    }
}