@import '../../scss/vars.scss';
@import '../../scss/mixins.scss';

.blogInfo {
    padding-top: 40px;
    padding-bottom: 40px;
    
    &__img {
        background-image: url(../../assets/img/resources/resource1.JPEG);
        background-size: cover;
        background-position: center;
        margin: 0 30px 30px 0;
        width: 350px;
        height: 300px;
        float: left;
    }

    &__headerTitle {
        @include text_bigTitle;

        font-weight: 400;
        color: #2f5c84;
    }

    &__text {
        @include text_regular;

        color: $grey_shades_70;
        font-weight: 500;
        line-height: 27px;
    }

    &__title {
        @include text_bigTitle;

        margin: 20px 0 20px 0;
    }
}

@media screen and (max-width: $lg) {
    .blogInfo {
        
        &__img {
            width: 250px;
            height: 200px;
        }
    
        &__headerTitle {
            font-size: 29px;
            line-height: 42px;
        }
    
        &__title {
            font-size: 29px;
            line-height: 42px;
        }
    }
}

@media screen and (max-width: $xs) {
    .blogInfo {
        
        &__img {
            width: 100%;
            height: 30vh;
        }
    }
}