@import '../../../scss/vars.scss';
@import '../../../scss/mixins.scss';

.resourceItem {
    display: flex;
    align-items: end;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 400px;
    padding: 20px;

    &__textPanel {
        @include text_title;

        padding: 18px;
        background-color: $general_background;
        opacity: 0.9;
        color: $white;
    }
}