@import '../../../scss/vars.scss';
@import '../../../scss/mixins.scss';

.homeHeader {
    position: fixed;
    height: 74px;
    width: 100%;
    padding: 0 12px, 0, 12px;
    z-index: 5;
    background-color: $general_background;

    @include flex-center;

    &--opacity {
        opacity: 0.9;
    }

    &__container {
        width: 100%;
        max-width: calc($maxContainerWidth + 32px + 32px);

        &Link {
            @include flex-center;

            gap: 20px;
        }

        &Title {
            color: $white;

            @include flex-center;
            @include text_regular;
        }
    }

    &__mMenuItem {
        display: none;
        color: $white;
        margin-right: 20px;
    }
}


.logo {
    background-image: url(../../../assets/img/logo.png);
    background-size: cover;
    background-position: center;
    width: 74px;
    height: 74px;
    color: $white;

    &:hover {
        background-image: url(../../../assets/img/logo-focus.png);
        background-size: 80px;
    }
}

.menu {
    display: none;
}

.menu a {
    color: $white;
    text-transform: uppercase;
    text-decoration: none;
    padding: 0 10px;
}

.show-menu-btn,
.hide-menu-btn {
    transition: 0.4s;
    font-size: 30px;
    cursor: pointer;
    user-select: none;
    display: none;
}

.show-menu-btn {
    margin-left: auto;
}

.show-menu-btn i {
    line-height: 100px;
}

.menu a:hover,
.show-menu-btn:hover,
.hide-menu-btn:hover {
    color: $grey_shades_50;
}

#chk {
    position: absolute;
    visibility: hidden;
    z-index: -1111;
}

@media screen and (max-width: $sm) {
    .homeHeader {

        &__container {
            display: flex;
            justify-content: space-between;
    
            &Link {
                display: none;
            }
        }

        &__mMenuItem {
            display: block;
        }
    }

    .show-menu-btn,
    .hide-menu-btn {
        display: block;
    }

    .menu {
        display: block;
        position: fixed;
        width: 100%;
        height: 100vh;
        // background: rgb(39, 16, 16);
        background-color: $general_background;
        right: -100%;
        top: 0;
        text-align: center;
        padding: 80px 0;
        line-height: normal;
        transition: 0.7s;
        z-index: 3;
    }

    .menu a {
        display: block;
        padding: 20px;
    }

    .hide-menu-btn {
        position: absolute;
        top: 30px;
        right: 30px;
        color: $white;
    }

    .mobile-menu-show {
      right: 0;
    }
}
  